.Game {
  text-align: center;
}

.Game-header {
  background-color: #222;
  height: 100px;
  padding: 30px;
  color: rgb(192, 25, 25);
}

.Game-title {
  font-size: 80px;
}

.Game-status {
  padding: 50px;
}

.Game-intro {
  font-size: 50px;
}

.Card {
  display: inline-block;
  border-color: white;
  border-width: 10px;
  border-style: solid;
  margin-bottom: 50px;
  margin-left: 50px;
  width: 120px;
  cursor: pointer;
  
}
.flipcard {
  background: rgb(68, 45, 45);
  transform-style: preserve-3d;
  transition: transform 0.6s;
  transform: rotateY(180deg);
}





.Matched {
  border-color: #55cc88;
}

.CardContainer {
  display: inline-block;
  max-width: 950px;
  margin: "20px auto";
  
}

img {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;   
}